.item-carousel {
  &-next,
  &-prev {
    width: 51px;
    height: 68px;
    background: url('data-url:./images/arrow-right.png') no-repeat top center/cover;
    @apply cursor-pointer;
    
    &.swiper-button-disabled {
      background-image: url('data-url:./images/arrow-left.png');

      @apply cursor-auto;
    }
  }

  &-prev {
    @apply rotate-180;
    
    &.swiper-button-disabled {
      @apply rotate-0;
    }
  }

  &-next {
    &.swiper-button-disabled {
      @apply rotate-180;
    }
  }
}

.screen-carousel {
  &-pagination {
    .swiper-pagination-bullet {
      &.swiper-pagination-bullet-active {
        @apply shadow-[0_0_46px_rgba(0,0,0,0.14)] text-gray-600;
      }
    }
  }
}

.testimonial-carousel {
  .swiper-slide-active {
    & > div {
      @apply bg-purple-500 text-white -translate-y-2;

      & > .quote {
        @apply brightness-0 invert;
      }
    }
  }
}
